@use '../colors';

.grid {
  display: -ms-grid;
  display: grid;
  -ms-grid-columns: 1fr 15px 1fr 15px 1fr 15px 1fr;
  grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
  grid-gap: 15px;
  width: 92.5%;
  margin: 0 auto;
  .grid_item {
    background: transparent;
    height: 200px;
    padding: 10px;
    margin: 0;
    text-align: center;
    border-radius: 0.35rem;
    box-shadow: 0.5px 0.5px 10px colors.$darkBlue;
    .button_div {
      padding-top: 35px;
    }
  }
}

.grid_item:hover {
  transform: scale(1.05);
  transition: 0.5s ease;
}

.grid_links {
  display: inline-block;
}

.work {
  text-align: center;
  margin-bottom: 50px;
}
